<template>
  <div>
    <h2 v-if="objTitle" v-html="objTitle" class="obj_title"></h2>
    <h5 v-if="objSubTitle" v-html="objSubTitle" class="obj_subtitle"></h5>
    <div v-if="objTemplate">
      <div v-for="templateItem in objTemplate" class="obj_templategroup">
        <u><p v-if="templateItem.name" v-html="templateItem.name" class="obj_templ_name"></p></u>
        <div v-if="templateItem.attributes">
          <s-attribute v-for="attr in templateItem.attributes" :attr="objOneObject.attributes[attr]" >
          </s-attribute>
        </div>
        <b-button-group v-if="templateItem.actions">
          <b-button variant="primary"
            v-for="attr in templateItem.actions"
            v-html="objOneObject.actions[attr].name"
            v-on:click="objActSwitch(attr)"
            :disabled="objOneObject.actions[attr].disabled"/>
        </b-button-group>
      </div>
    </div>
    <div v-if="objAttributes">
      <s-attribute v-for="attr in objAttrs.unused" :attr="objAttributes[attr]">
      </s-attribute>
    </div>
    <div v-if="objActions" >
      <b-button variant="danger"
        v-for="attr in objAttrs.actunused"
        v-html="objActions[attr].name"
        v-on:click="objActSwitch(attr)"
        :disabled="objActions[attr].disabled"
         />
    </div>
    <div v-if="objListObject" class="obj_list">
      <hr class="obj_list_hr">
      <div class="obj_list_el"
        v-for="(lkey,index) in objListObject">
        <in-object
          :objid="objid"
          :listid="index"
          :lkey="objid+lkey.id"/>
        <b-button
          block
          variant="outline-success lg"
          class="obj_btn"
          @click="objActiveSet(lkey.id)">
            <s-dblright class="obj_dbl_arr"/>
        </b-button>
      </div>
    </div>
    <s-action
      v-if="objInfo.actid"
      :objid="objid"
      :listid="listid"
      :actid="objInfo.actid"
      @done="objActionDone()"
    ></s-action>

  </div>
</template>
<script>
  import singleAttribute from './attribute.vue';
  import singleAction from './action.vue';

  import {mapGetters} from 'vuex';
  import { mapMutations } from 'vuex';
  import { mapActions } from 'vuex';
  import {swW2H} from './stringlib.js';

  import dblAngRight from '../assets/double-angle-right.svg';

  export default {
    props:['objid','listid'],
    name: "in-object",
    components:{
      's-attribute': singleAttribute,
      's-action': singleAction,
      's-dblright':dblAngRight
    },
    data () {
      return {
        AttrList:{
          used:["title","subtitle"],
          unused:[],
          actused:[],
          actunused:[]
        },
        objInfo:{
          actid: false,
          obj:{
            id: this.objid, //undefined,
            listid: this.listid//undefined,
          },
        }
      }
    },
    methods:{
      ...mapActions(['sendAct']),
      ...mapMutations(['objActiveSet']),
      // objObj(){
      //   this.objInfo.obj.id=this.objid;
      //   this.objInfo.obj.listid=this.listid;
      // },
      // objAttrClean(){
      //   this.AttrList.used = ["title","subtitle"],
      //   this.AttrList.unused=[],
      //   this.AttrList.actused=[],
      //   this.AttrList.actunused=[]
      // },
      objActSwitch(actName){
        this.objInfo.actid=actName;
        if("user_params" in this.objOneObject.actions[actName])
        {
          this.actStat=true;
        }
        else
        { console.log("Object send: "+ JSON.stringify(this.objInfo));
          this.sendAct(this.objInfo);
          this.objInfo.actid=false;
          this.actStat=false;
        }
        return false;
      },
      objGetOneObject(){
         console.log("Object getter!\n");
         return this.oneObject(this.objInfo.obj);
      },
      objActionDone()
      {
        this.objInfo.actid=false;
        return false;
      }
    },
    computed: {
      ...mapGetters(['oneObject']),
      objOneObject(){
        if(typeof this.objInfo.obj.listid === 'number'){
          return this.objGetOneObject();
        }
        else return this.objGetOneObject().object;
      },
      objListObject(){
        if('list' in this.objGetOneObject())
          if('objects' in this.objGetOneObject().list)
            return this.objGetOneObject().list.objects;
        return false;
      },
      objAttributes(){
        if('attributes' in this.objOneObject) return this.objOneObject.attributes;
        else return false;
      },
      objActions(){
        if('actions' in this.objOneObject) return this.objOneObject.actions;
        else return false;
      },
      objTitle(){
        if(this.objAttributes)
          if("title" in this.objAttributes)
            if("value_description" in this.objAttributes.title)
              return swW2H(strthis.objAttributes.title.value_description);
            else return swW2H(this.objAttributes.title.value);
        return false;
      },
      objSubTitle(){
        if(this.objAttributes)
          if("subtitle" in this.objAttributes)
            if("value_description" in this.objAttributes.title)
              return swW2H(strthis.objAttributes.title.value_description);
            else return swW2H(this.objAttributes.title.value);
        return false;
      },
      objTemplate(){
        if("template" in this.objOneObject)
          if("groups" in this.objOneObject.template){
            return this.objOneObject.template.groups;
        }
        return false;
      },
      objAttrs(){
        if(this.objTemplate )
        {
          this.objTemplate.forEach(
            l_item => {
              if("attributes" in l_item) this.AttrList.used.push(...l_item.attributes);
              if("actions" in l_item) this.AttrList.actused.push(...l_item.actions);
            });
        }
        if(this.objAttributes)
        {
          for(let lkey in this.objAttributes){
            if(this.AttrList.used.indexOf(lkey)<0) this.AttrList.unused.push(lkey);
          }
        }
        if(this.objActions)
        {
          for(let lkey in this.objOneObject.actions){
            if(this.AttrList.actused.indexOf(lkey)<0) this.AttrList.actunused.push(lkey);
          }
        }
        return this.AttrList;
      }
    }
  }
</script>
