import Vue from 'vue';
import Vuex from 'vuex';

import {json1,json2} from './jsons.js';

Vue.use(Vuex);

export const store = new Vuex.Store({
	state: {
		objActive:"0",
		data: {
    }
	},
	getters: {
		objActiveGet(state){return state.objActive;},
		oneObject: state =>{ return lobj=>{
				console.log(lobj);
				if(typeof lobj !== 'undefined'){
					console.log(1);
					if(typeof lobj.id === "string"){
						console.log(2);
					 	if(typeof lobj.listid === "number")
						{
							console.log("3 "+ lobj.listid);
							return state.data[lobj.id].list.objects[lobj.listid];
						}
						else return state.data[lobj.id];
				 	} else return state.data[state.objActive];
				}	else return state.data[state.objActive];
			}
		},
		menuObjects(state){
			let llist=[]; let ltitle;
			for(let lkey in state.data){
				ltitle=false;
				if("attributes" in state.data[lkey].object)
					if("title" in state.data[lkey].object.attributes)
						if("value_description" in state.data[lkey].object.attributes.title)
							ltitle=state.data[lkey].object.attributes.title.value_description;
					 	else ltitle=state.data[lkey].object.attributes.title.value;
				llist.push({key:lkey, title:ltitle});
			}
			return llist;
		}
	},
	mutations: {
		objActiveSet(state,lobj){
			state.objActive=lobj;
		},
		dataListFill(state, lstr)
		{
			let lobj=JSON.parse(lstr).list.objects;
			let lobj2={};
			for (let l in lobj)
			{

				lobj2[lobj[l].id]={object:lobj[l]};
			}
			state.data=Object.assign({},state.data,lobj2);
		},
		dataFill(state, lstr)
		{
			let lobj=JSON.parse(lstr);
			let lobj2={}; lobj2[lobj.object.id]=lobj;
			state.data=Object.assign({},state.data,lobj2);
		},
		//добавляем необходимые поля в объект правильным образом
		parSet(state,payload){
			let lact={};
			if(typeof payload.obj.listid === "number")
				lact=store.getters.oneObject(payload.obj).actions[payload.actid];
			else lact=store.getters.oneObject(payload.obj).object.actions[payload.actid];
			if("valid" in lact) return false;
			else {
				if("user_params" in lact){
					Vue.set(lact,'valid',0);
					let lpar;
					for(lpar in lact.user_params){
						Vue.set(lact.user_params[lpar],'value',NaN);
						Vue.set(lact.user_params[lpar],'valid',0);
					}
				}
				else Vue.set(lact,'valid',1);
			}
			return true;
		},
		//изменяем поля объекта, по мере заполнения параметров
		parChange(state,payload){
			let lact={};
			if(typeof payload.objInfo.obj.listid === "number")
				lact=store.getters.oneObject(payload.objInfo.obj).actions[payload.objInfo.actid];
			else lact=store.getters.oneObject(payload.objInfo.obj).object.actions[payload.objInfo.actid];
			let lpar;
			let lvalid=1;
			for(lpar in lact.user_params){
				if(lact.user_params[lpar].code==payload.data.code)
				{
					lact.user_params[lpar].value=payload.data.value;
					lact.user_params[lpar].valid=payload.data.valid;
				}
				lvalid=lvalid*lact.user_params[lpar].valid;
			}
			lact.valid=lvalid;
	  }
	},
	actions: {
		sendAct(store,payload){
			console.log(JSON.stringify(payload.obj));
			let lact={};
			if(typeof payload.obj.listid === "number")
				lact=store.getters.oneObject(payload.obj).actions[payload.actid];
			else lact=store.getters.oneObject(payload.obj).object.actions[payload.actid];

			let lsend={
				request_id: 1,
    		type: "make_action",
    		data : {
					code: lact.code,
					params: lact.params
				}
			}
			if("user_params" in lact){
				lact.user_params.forEach(
					litem => {
						lsend.data[litem.code]=litem.value;
					});
			}
			console.log("Input: "+payload.actid+"\n"+JSON.stringify(lsend));
			alert("Input: "+payload.actid +"\n"+JSON.stringify(lsend));

			// store.commit('orderSend');
			//
			// setTimeout(() => {
			// 	console.log(payload);
			// 	store.commit('orderDone');
			// }, 1000);
		}
	},
	strict: process.env.NODE_ENV !== 'production'
});

store.commit('dataFill',JSON.stringify(json1()));
store.commit('dataFill',JSON.stringify(json2()));
store.commit('dataListFill',JSON.stringify(json2()));
